/**
 * axios 
 * Created by sunlf on 2017/7/21.
 */
import axios from 'axios'
import Message from 'common/message'
import {baseURL} from 'config/global.toml'
import {LoadingTimeOut} from 'config/info.toml'
import Cache from 'common/cache'
import Router from 'router'

// axios 
axios.defaults.timeout = 5000
axios.defaults.baseURL = baseURL

// http
// var loadinginstace
axios.interceptors.request.use(config => {
  // element ui Loading
  // loadinginstace = ElementUI.Loading.service({fullscreen: true})

  const accessToken = Cache.get('access_token')
  const userToken = Cache.get('user_token')
  if (accessToken && userToken) {  // tokenhttp headertoken
    config.headers.AccessToken = accessToken
    config.headers.JSESSIONID = userToken
  }
  console.log(`[kalix]-[axios.js] axios interceptor request config is `, config)
  return config
}, error => {
  // loadinginstace.close()
  Message.error({
    message: LoadingTimeOut
  })
  return Promise.reject(error)
})

//    http
if (navigator.userAgent.indexOf('Firefox') === -1) {
  // http
  axios.interceptors.response.use(res => {
    // loading
    // loadinginstace.close()
    console.log(`[kalix]-[axios.js] axios interceptor response data is `, res)
    if (res.data.code === 401) {
      Cache.clear()
      Router.push({path: '/login'})
      return
    }
    return res
  }, err => {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = '(400)'
          break
        case 401:
          err.message = '(401)'
          break
        case 403:
          err.message = '(403)'
          break
        case 404:
          err.message = '(404)'
          break
        case 408:
          err.message = '(408)'
          break
        case 500:
          err.message = '(500)'
          break
        case 501:
          err.message = '(501)'
          break
        case 502:
          err.message = '(502)'
          break
        case 503:
          err.message = '(503)'
          break
        case 504:
          err.message = '(504)'
          break
        case 505:
          err.message = 'HTTP(505)'
          break
        default:
          err.message = `(${err.response.status})!`
      }
    } else {
      err.message = '!'
    }
    Message.error(err.message)
    // loadinginstace.close()
    // Message.error({
    //   message: LoadingFailure
    // })
    return Promise.reject(err)
  })
}
export default axios



